<template>
  <div class="page-container" style="min-height:443px">

    <!-- Page content -->
    <div class="page-content">

      <!-- Main content -->
      <div class="content-wrapper">

        <!-- Page header -->
        <div class="page-header page-header-default">
          <div class="content-group ">
            <div class="panel-body bg-blue border-radius-top text-center"
                 style="background-image: url(/static/images/bg.png);
                        background-size: contain;">
              <div class="content-group-sm">
                <h1 class="text-semibold no-margin">
                  {{fullname}}
                </h1>

                <h5 class="display-block no-margin">{{userInfo.occupation}}</h5>
              </div>

              <a href="#" class="display-inline-block content-group-sm">
                <img :src="userInfo.image" class="img-circle img-responsive" alt=""
                     style="width: 120px; height: 120px;">
              </a>
            </div>

          </div>
        </div>
        <!-- /page header -->


        <!-- Content area -->
        <div class="">
          <div class="row">
            <div class="col-md-6">
              <div class="panel panel-body">
                <div class="form-group mt-5">
                  <label class="text-semibold">Full name:</label>
                  <span class="pull-right-sm">{{fullname}}</span>
                </div>

                <div class="form-group">
                  <label class="text-semibold">Phone number:</label>
                  <span class="pull-right-sm">{{userInfo.phone}}</span>
                </div>

                <div class="form-group">
                  <label class="text-semibold">Email:</label>
                  <span class="pull-right-sm">{{userInfo.email}}</span>
                </div>

                <div class="form-group">
                  <label class="text-semibold">Address:</label>
                  <span class="pull-right-sm">{{userInfo.address}}</span>
                </div>

                <div class="form-group">
                  <label class="text-semibold">Birthday:</label>
                  <span class="pull-right-sm">{{moment(userInfo.birthday).format('DD/MM/YYYY')}}</span>
                </div>

                <div class="form-group no-margin-bottom">
                  <label class="text-semibold">Website:</label>
                  <span class="pull-right-sm">
                    <a v-if="!editMode" :href="userInfo.website">{{userInfo.website}}</a>
                    <input v-else v-model="userInfo.website">
                  </span>
                </div>

                <button class="btn btn-default pull-right mt-20" @click="editMode = !editMode">
                  {{editMode ? 'Save' : 'Edit' }}
                </button>
              </div>
            </div>
            <div class="col-md-6">
              <div class="panel">
                <div class="panel-body border-top-primary text-center">
                  <h6 class="no-margin text-semibold">Storge</h6>
                  <p class="content-group-sm text-muted">your package has {{freeSpace}} GB free space left</p>

                  <div class="progress content-group-sm">
                    <div class="progress-bar progress-bar-primary" :data-transitiongoal-backup="storageFreePrecentage"
                         :data-transitiongoal="storageFreePrecentage"
                         :style="{ width: storageFreePrecentage + '%'}" :aria-valuenow="storageFreePrecentage">
                      {{storageFreePrecentage}}%
                    </div>
                  </div>
                </div>
                <div class="panel-footer">
                  <div class="heading-elements">
                    <router-link to="/pricing-layout" class="btn btn-info pull-right">Upgrade</router-link>
                  </div>
                </div>
              </div>

              <div class="panel panel-body">
                <label for="" class="text-semibold">Additinal info:</label>
                <p>{{userInfo.additionalInfo}}</p>
              </div>

            </div>
          </div>

          <div class="row">
            <div class="col-md-6">
              <div class="panel">
                <div class="panel-heading">
                  <h6 class="panel-title">Notifications settings</h6>
                </div>
                <div id="notifications-panel" class="panel-body border-top-primary text-center">
                  <div v-for="notification in notificationSettings" class="checkbox checkbox-right checkbox-switchery">
                    <label class="display-block ">
                      <input type="checkbox" v-model="notification.enabled">
                      <label class="pull-left">{{notification.text}}</label>
                    </label>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>


    </div>
    <!-- /content area -->

  </div>
  <!-- /main content -->
</template>

<script>
  import moment from 'moment'
  export default {
    name: 'profile-settings',
    data () {
      return {
        editMode: false,
        userInfo: {
          firstName: 'Nave',
          lastName: 'Eni',
          phone: '052-6595639',
          email: 'nave@stanga.com',
          address: 'tzfat 13',
          birthday: new Date(),
          website: 'http://www.website.com',
          occupation: 'Programmer',
          additionalInfo: 'this is some information about myself.',
          image: '/static/images/demo/users/face4.jpg'
        },
        notificationSettings: [
          {
            text: 'Send Email notification when partner upload / download files',
            enabled: true
          },
          {
            text: 'Send Email notification when partner creates folder',
            enabled: false
          },
          {
            text: 'Send Email notification when partner upload / download files',
            enabled: true
          },
          {
            text: 'Send Email notification when partner upload / download files',
            enabled: true
          }
        ],
        storage: {
          usedSpace: 500,
          totalSpace: 2000
        }
      }
    },
    computed: {
      fullname: function () {
        return this.userInfo.firstName + ' ' + this.userInfo.lastName
      },
      freeSpace: function () {
        return this.storage.totalSpace - this.storage.usedSpace;
      },
      storageFreePrecentage: function () {
        return Math.round((this.storage.usedSpace / this.storage.totalSpace) * 100);
      }
    },
    methods: {
      moment
    }
  }
</script>



