<template>
  <div class="page-container" style="min-height:443px">

    <!-- Page content -->
    <div class="page-content">

      <!-- Main content -->
      <div id="content" class="content-wrapper">

        <!-- Page header -->
        <div class="page-header page-header-default">
          <div class="page-header-content">
            <div class="page-title">
              <h4><span class="text-semibold">Dashboard</span>
              </h4>
              <a class="heading-elements-toggle"><i class="icon-more"></i></a></div>
          </div>

        </div>
        <!-- /page header -->


        <!-- Content area -->
        <div class="content">
          <div class="row" style="padding-bottom: 10px">
            <button type="button" class="btn border-info text-info-600 btn-flat btn-icon mb-10"
                    data-toggle="modal" data-target="#add_project_modal">
              <i class="icon-plus-circle2"></i>
              Add new project
            </button>
          </div>
          <div class="row childs-padding-to-margin">
            <div v-for="project in projects" class="col-sm-4">
              <div class="thumbnail">
                <div class="panel-heading">
                  <h6 class="panel-title">{{project.name}}<a class="heading-elements-toggle"><i
                    class="icon-more"></i></a>
                  </h6>
                  <div class="heading-elements">
                    <ul class="icons-list">
                      <li>
                        <a @click="editedProject = clone(project)" data-toggle="modal"
                           data-target="#edit_project_modal"><i
                          class="icon-pencil"></i></a>
                      </li>
                    </ul>
                  </div>
                </div>

                <div class="thumb">
                  <img :src="project.image" alt="">
                  <div class="caption-overflow" style="cursor: pointer">
										<span>
											<a href="projectExplorer.html" class="btn bg-teal-300 btn-rounded btn-icon"
                         data-popup="tooltip" data-placement="bottom" data-original-title="view files">
                        <i class="icon-folder4"></i></a>
											<a class="btn bg-teal-300 btn-rounded btn-icon"
                         data-popup="tooltip" data-placement="bottom" data-original-title="share"
                         @click="editedProject = clone(project)" data-toggle="modal"
                         data-target="#add_partners_to_project_modal">
                        <i class="icon-share3"></i></a>
										</span>
                  </div>
                </div>

                <div class="caption">
                  {{project.description}}
                </div>
              </div>
            </div>
          </div>
        </div>
        <!-- /content area -->

      </div>
      <!-- /main content -->

    </div>
    <!-- /page content -->

    <projectModal id="add_project_modal" mode="Add"
                  @saved="addProject($event)"></projectModal>
    <projectModal id="edit_project_modal" mode="Edit"
                  :project="editedProject"
                  @saved="editProject($event)"></projectModal>
    <add-partners-modal id="add_partners_to_project_modal"
                        :addToProject="true"
                        :projectName="editedProject.name"></add-partners-modal>
  </div>
</template>

<script>
  import projectModal from './ProjectModal.vue'
  import AddPartnersModal from '@/components/partners/AddPartnersModal.vue'
  import storageService from '../../services/storageService.js'
  export default {
    name: 'dashboardLayout',
    components: {projectModal, AddPartnersModal},
    data () {
      return {
        projects: [{
          id: 1,
          name: 'Nave\'s project',
          description: 'this project was made for example purposes',
          image: '/static/images/demo/flat/15.png'
        }],
        editedProject: null
      }
    },
    created(){
      this.editedProject = this.clone(this.projects[0]); // must send clone of project, so that original wont be updated untill save

      //dummy data
      var clone1 = this.clone(this.projects[0]);
      clone1.id = 2;
      this.projects.push(clone1);

      var clone2 = this.clone(this.projects[0]);
      clone2.id = 3;
      this.projects.push(clone2);
    },
    methods: {
      clone: function (object) {
        return JSON.parse(JSON.stringify(object))
      },
      addProject: function (project) {
        this.projects.push(project);
        storageService.createUserStorage();
      },
      editProject: function (project) {
        this.projects.forEach(function (vProject) {
          if (vProject.id === project.id) {
            Object.assign(vProject, project);
          }
        })
      }
    }
  }
</script>

<style scoped>
  .childs-padding-to-margin > div {
  }
</style>



