<template>
  <div class="content ">
    <div class="row">
      <div class="search-component">
        <div class="input-group">
          <input type="text" class="form-control" name="field-2" placeholder="Search a partner..."
                 v-model="partnerFilter">
              <span class="input-group-btn">
                <button class="btn btn-primary" type="button">
                  <span class="glyphicon glyphicon-search"></span>
                </button>
              </span>
        </div>
      </div>
    </div>
    <div class="row">
      <ul class="media-list list-hover">
        <li v-for="partner in filteredPartners" class="media panel-body">
          <div class="media-left">
            <a href="#"><img :src="partner.image" class="img-circle img-md" alt=""></a>
          </div>
          <div class="media-body">
            <div class="media-heading">
              <a href="#">{{partner.name}}</a>
            </div>
            <span class="text-muted">{{partner.occupation}}</span>
          </div>
          <div class="media-right media-middle">
            <button v-if="partner.isFriend"
                    @click="removeFriend(partner)"
                    class="label label-icon-lg label-success">
              <i class="icon-checkmark4 position-left"></i>
              {{ addToProject ? 'Added' : 'partners'}}
            </button>
            <button v-else class="btn btn-primary" @click="addFriend(partner)">Add partner</button>
          </div>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'partners-searcher',
    props: {
      addToProject: false
    },
    data () {
      return {
        partners: [
          {
            image: '/static/images/demo/users/face4.jpg',
            name: 'Neta Sha',
            occupation: 'Programmer',
            isFriend: false
          },
          {
            image: '/static/images/demo/users/face1.jpg',
            name: 'Some One',
            occupation: 'Designer & UI Expert',
            isFriend: true
          },
          {
            image: '/static/images/demo/users/face3.jpg',
            name: 'Per Son',
            occupation: 'Leading photographer',
            isFriend: false
          }
        ],
        partnerFilter: ''
      }
    },
    computed: {
      filteredPartners: function () {
        var partnerFilter = this.partnerFilter.toLowerCase();
        return this.partners.filter(function (partner) {
          return partner.name.toLowerCase().includes(partnerFilter)
        });
      }
    },
    methods: {
      addFriend: function (partner) {
        partner.isFriend = true;
      },
      removeFriend: function (partner) {
        partner.isFriend = false;
      }
    }
  }
</script>

<style scoped>
  .search-component {
    border-bottom: 1px solid #00bcd4;
    padding-bottom: 10px;
  }
</style>



